import React from 'react';

export type WorkProps = {
    workName: string;
    workDescription: string;
    workImage: string;
    classJudge: string;
    isVisible?: boolean;
    videoURL?: string;
    pageURL?: string;
}

export const Work: React.FC<WorkProps> = ({
    workName,
    workDescription,
    workImage,
    classJudge,
    isVisible = true,
    videoURL,
    pageURL,
}) => {
    if (!isVisible) return null;
    if (videoURL!="" && videoURL!=null) {
        return (
            <div className="work">
                <img src={workImage} alt="workImage"/>
                <div className="sentences">
                    <h3 className={classJudge}>{workName}</h3>
                    <p>{workDescription}</p>
                    <a href={videoURL} target="_blank" rel="noreferrer">＞Videoを見る</a>
                </div>
            </div>
        );
    } else if (pageURL!="" && pageURL!=null)　{
        return (
            <div className="work">
                <img src={workImage} alt="workImage"/>
                <div className="sentences">
                    <h3 className={classJudge}>{workName}</h3>
                    <p>{workDescription}</p>
                    <a href={pageURL} target="_blank" rel="noreferrer">＞Pageを見る</a>
                </div>
            </div>
        );
    } else {
        return (
            <div className="work">
                <img src={workImage} alt="workImage"/>
                <div className="sentences">
                    <h3 className={classJudge}>{workName}</h3>
                    <p>{workDescription}</p>
                </div>
            </div>
        );
    }
};